export function overrideDefaultTheme(config: object) {
  return merge(config, merge(DEFAULT_THEME.config, {})) as ThemeConfig;
}

function merge(source: object, target: object) {
  for (const [key, val] of Object.entries(source)) {
    if (val !== null && typeof val === `object`) {
      // @ts-ignore
      if (target[key] === undefined) {
        // @ts-ignore
        target[key] = {};
      }
      // @ts-ignore
      merge(val, target[key]);
    } else {
      // @ts-ignore
      target[key] = val;
    }
  }
  return target;
}
